import React from 'react';
import styles from "./browseUserPopupBody.module.css";
import {EmploymentTypes, EmploymentTypesUa, User, UserTypes, UserTypesUa} from "../../../../models/models";
import {fullName} from "../../../../helpers/helpers";

interface PropTypes {
  user: User;
};

const BrowseUserPopupBody: React.FC<PropTypes> = ({user}) => {
  return (
    <div>
      {!user.verified && <div className={styles.verification}>
        <h2>  !</h2>
        <p>        .</p>
      </div>}
      <div className={styles.list}>
        <div><span>ID: </span><span>{user.id}</span></div>
        <div><span>...: </span><span>{fullName(user)}</span></div>
        <div><span>: </span><span>{UserTypesUa[user.type as UserTypes]}</span></div>
        {user.studentInfo && <div><span>: </span><span>{user.studentInfo.degree.name}</span></div>}
        {user.employeeInfo && <div><span>: </span>
            <span>{EmploymentTypesUa[user.employeeInfo.employmentType as EmploymentTypes]}</span></div>}
        <div><span>: </span><span>{user.department ? user.department.name : ''}</span></div>
        <div><span>E-mail: </span><span>{user.email}</span></div>
        <div><span>.: </span><span>{user.phoneNumber}</span></div>
        {user?.extraPhoneNumbers && <div><span> .: </span>
          <ul>{JSON.parse(user.extraPhoneNumbers).map((item: string) => <li>{item}</li>)}</ul>
        </div>}
      </div>
    </div>
  );
}

export default BrowseUserPopupBody;